// import Swiper JS
import Swiper, { Navigation }  from 'swiper';
Swiper.use([Navigation]);

// import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';

const swiper = new Swiper('.carousel', {
  speed: 450,
  slidesPerView: 1,
  spaceBetween: 4,
  // loop: true,
  navigation: {
    nextEl: '.home-insights__arrowright',
    prevEl: '.home-insights__arrowleft',
  },
  breakpoints: {
    700: {
      slidesPerView: 2,
    },
    1360: {
      slidesPerView: 3,
    }
  }
});